import { _decorator, Component, Node, v3 } from 'cc';
import { Enemy } from './Enemy';
const { ccclass, property } = _decorator;

@ccclass('Missile')
export class Missile extends Component {
    start() {

    }
    update(deltaTime: number) {
        this.rotation();
        let missile = this.node.position;
        let enemy = this.node.parent.getChildByName("enemy").getComponent(Enemy).node.position;
        console.log(enemy)

    }
    //旋转
    rotation() {
        let theangle = Math.atan2(this.node.position.y - this.node.parent.getChildByName("enemy").position.y,
            this.node.position.x - this.node.parent.getChildByName("enemy").position.x);
        let theta = theangle * 180 / Math.PI;
        this.node.eulerAngles = v3(0, 0, theta);
    }

    die() {
        this.node.destroy();
    }
}
